@import "../../../scss/helpers/placeholder";
@import "../../../scss/helpers/mixins";

$prefix: '.wc-booster';

.wp-block-wc-booster-section{
    position: relative;
    background-repeat: no-repeat !important;
    background-size: cover;
    z-index: 1;

    #{$prefix}-section-wrapper {
        margin: 0 auto;
        padding: 15px;

        &#{$prefix}-boxed{
            max-width: 1140px;
            @include media(sm) {
                max-width: 720px;
            }

            @include media(xs) {
                max-width: 540px;
            }
        }

        #{$prefix}-section-inner {
            position: relative;
        }
    }

    #{$prefix}-section-overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;      
        z-index: 1;
    }

    p:empty{
        display: none;
    }

    .block-editor-inner-blocks,
    .wc-booster-section-inner {
        position: relative;
        z-index: 9;
    }
}

#{$prefix}-section-shape {
    overflow: hidden;
    position: absolute;
    left: 0;
    width: 100%;
    line-height: 0;
    direction: ltr;
    transform: rotate(180deg);   


    &.vertically-flipped {
        // bottom: 100%;
        // top: auto;
        transform: rotate(0);
    }

    &.horizontally-flipped {
        svg {
            transform: scaleX(1) scaleY(-1) rotate(180deg);
        }
    }

    .divider--waves {       
        path:nth-child(2) {
            opacity: 0.3;
        }
        path:nth-child(1) {
            opacity: 0.15;
        }
    }

    svg {
        height: 150px;
        @include media(sm) {
            height: 100px;
        }
        @include media(mb) {
            height: 50px;
        }
    }

    &.top {
        bottom: 100%;
    }

    &.bottom {
        top: 100%;
    }
}

.wp-block-wc-booster-section,
.wc-booster-section-wrapper {
    > video {
        position: absolute;
        z-index: 0;
        object-fit: cover;
        width: 100%;
        height: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

} 
